import BasicItem from "jx/editor/editionitems/BasicItem";
export default class NumberItem extends BasicItem {
    constructor(dataObject) {
        super(Object.assign({
            icoTitle: "",
            icoSymbol: "gear",
            actions: [],
            informations: [],
            backgroundColor: "#efefef"
        }, dataObject));
        //console.log(dataObject, this.dataObject)
        this.content = $(`<input  id="value" type="number"  class="form-control"  value="">`);
        this.view.find("#content").html(this.content);
        this.view.find("#value").prop('readonly', !this.editable);
        this.view.find("#value").attr('min', this.data.min);
        this.view.find("#value").attr('max', this.data.max);
        this.view.find("#value").attr('step', this.data.step);
        this.view.find("#value").prop('value', this.data.value || "");
        this.view.on("change", (event) => {
            if (this.autoRefresh)
                this._refreshData();
            this.onchanged.dispatch({ target: this });
        });
    }
    _refreshData() {
        this.data.value = this.value;
    }
    _refreshView() {
        this.data.value = this.value;
    }
    get value() {
        return Number(this.view.find("#value")[0].value) || 0;
    }
    set value(data) {
        this.view.find("#value").val(data);
        this._refreshData();
    }
    set lock(value) {
        if (!value) {
            this.view.find("#value").prop('readonly', !this.editable);
        }
        else {
            this.view.find("#value").prop('readonly', true);
        }
    }
}
